import BN from 'bn.js';

export class FixedFees {
  enum: number = 1;
  version: number;
  protocolFeeBps: BN;
  settlementFeeBps: BN;
  _padding1: number = 0;
  _padding2: number = 0;
  _padding8: BN = new BN(0);
  _padding16: BN = new BN(0);
  _padding8_0: BN = new BN(0);
  _padding8_1: BN = new BN(0);
  _padding8_2: BN = new BN(0);
  _padding8_3: BN = new BN(0);

  constructor(args: {
    version: number;
    protocolFeeBps: number;
    settlementFeeBps: number;
  }) {
    this.version = args.version;
    this.protocolFeeBps = new BN(args.protocolFeeBps);
    this.settlementFeeBps = new BN(args.settlementFeeBps);
  }
}
