import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  SYSVAR_CLOCK_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { serialize } from 'borsh';

import { TraderPositionAccountArgs, schema } from '../borsh';

export const createTraderPositionAccountInstruction = async (
  programId: PublicKey,
  settings: TraderPositionAccountArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  traderPositionAccountPubkey: PublicKey,
  parimutuelPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));
  const payerPubkey = userWalletPubkey;

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: traderPositionAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_CLOCK_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};
