import { PublicKey } from '@solana/web3.js';
import _uniqWith from 'lodash/uniqWith';
import _isEqual from 'lodash/isEqual';

import { ParimutuelWeb3 } from '../web3';
import { MarketPubkey, ParimutuelAccount } from '../types';
import {
  getUpcomingTimes,
  getHistoricalTimes,
  decodeParimutuel,
  findParimutuelPubkey,
  notEmpty,
  getMultipleAccounts,
} from '../utils';

export async function getParimutuel(
  this: ParimutuelWeb3,
  parimutuelPubkey: PublicKey
): Promise<ParimutuelAccount> {
  const parimutuelAccount = await this.connection.getAccountInfo(
    parimutuelPubkey
  );

  if (!parimutuelAccount) {
    throw new Error('no parimutuel account');
  }

  return {
    pubkey: parimutuelPubkey,
    account: parimutuelAccount,
    info: {
      parimutuel: decodeParimutuel(parimutuelAccount.data),
    },
  };
}

export async function getParimutuels(
  this: ParimutuelWeb3,
  markets: MarketPubkey[],
  number?: number
): Promise<ParimutuelAccount[]> {
  if (!markets) return [];
  const { PARIMUTUEL_PROGRAM_ID } = this.config;

  const upcoming = markets
    .map((market) => {
      const { duration } = market;
      return getUpcomingTimes(
        market.pubkey.toBase58(),
        duration * 1000,
        number ? number : 5
      );
    })
    .flat();

  const historical = markets
    .map((market) => {
      const { duration } = market;
      return getHistoricalTimes(
        market.pubkey.toBase58(),
        duration * 1000,
        number ? number : 5
      );
    })
    .flat();

  // get unique time
  const times = _uniqWith([...historical, ...upcoming], _isEqual);

  const parimutuelPubkeys = await Promise.all(
    times.map(async (time) => {
      const pubkey = await findParimutuelPubkey(
        PARIMUTUEL_PROGRAM_ID,
        new PublicKey(time.marketPubkey),
        0,
        time.startTime,
        time.startTime
      );
      return pubkey;
    })
  );

  const parimutuelAccounts = await getMultipleAccounts(
    this.connection,
    parimutuelPubkeys.map((pubkey) => pubkey)
  );

  const filteredAccounts = parimutuelAccounts
    .map((parimutuel, index) => {
      if (!parimutuel) return null;
      return {
        pubkey: parimutuelPubkeys[index],
        account: parimutuel.account,
        info: {
          parimutuel: decodeParimutuel(parimutuel.account.data),
        },
      };
    })
    .filter(notEmpty);

  return filteredAccounts;
}

