import { PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { Store } from '../borsh';
import { decodeFixedFees, decodeStore } from '../utils';

export async function getStore(
  this: ParimutuelWeb3,
  storePubkey: PublicKey
): Promise<Store> {
  const account = await this.connection.getAccountInfo(storePubkey);

  if (!account) {
    throw new Error('Failed to find account');
  }

  const data = Buffer.from(account.data);
  const store = decodeStore(data);
  const storeFees = decodeFixedFees(data.slice(-65));
  store.fee = storeFees;
  return store;
}
