import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { serialize } from 'borsh';

import { TraderAccountsArgs, schema } from '../borsh';

export const createTraderAccountInstruction = async (
  programId: PublicKey,
  settings: TraderAccountsArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  protocolTokenAccountPubkey: PublicKey,
  settlementTokenAccountPubkey: PublicKey,
  networkTraderAccountPubkey: PublicKey,
  marketTraderAccountPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));
  const payerPubkey = userWalletPubkey;

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: true,
        isWritable: true,
      },
      {
        pubkey: protocolTokenAccountPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: settlementTokenAccountPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkTraderAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: marketTraderAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};
