import { PublicKey } from '@solana/web3.js';
import {
  TOKEN_PROGRAM_ID,
  ASSOCIATED_TOKEN_PROGRAM_ID,
} from '@solana/spl-token';
import BN from 'bn.js';

import {
  NETWORK_SEED,
  MARKET_SEED,
  PARIMUTUEL_SEED,
  MARKET_TRADER_ACCOUNT_SEED,
  NETWORK_TRADER_ACCOUNT_SEED,
  HONEYPOT_SEED,
  TRADER_POSITION_ACCOUNT_SEED,
  TRADER_ENTRY_ACCOUNT_SEED,
  NETWORK_FEE_PAYER_SEED,
  TRADER_SEQUENCE_SEED,
  STORE_SEED,
} from '../constants';
import { findProgramAddress } from '../utils/utils';

export const findNetworkPubkey = async (
  programId: PublicKey
): Promise<PublicKey> => {
  const [pubkey] = await findProgramAddress(
    [Buffer.from(NETWORK_SEED), new Uint8Array(programId.toBuffer())],
    programId
  );

  return new PublicKey(pubkey);
};

export const findMarketPubkey = async (
  programId: PublicKey,
  underlyingProductOraclePubkey: PublicKey,
  underlyingPriceOraclePubkey: PublicKey,
  protocolFeeCollectorPubkey: PublicKey,
  settlementFeeCollectorPubkey: PublicKey,
  duration: number,
  contractSize: number
): Promise<PublicKey> => {
  const [pubkey] = await findProgramAddress(
    [
      Buffer.from(MARKET_SEED),
      underlyingProductOraclePubkey.toBuffer(),
      underlyingPriceOraclePubkey.toBuffer(),
      protocolFeeCollectorPubkey.toBuffer(),
      settlementFeeCollectorPubkey.toBuffer(),
      new BN(duration).toBuffer(),
      new BN(contractSize).toBuffer(),
    ],
    programId
  );

  return new PublicKey(pubkey);
};

export const findParimutuelPubkey = async (
  programId: PublicKey,
  marketPubKey: PublicKey,
  marketOpen: number,
  timeWindowStart: number,
  marketClose: number
): Promise<PublicKey> => {
  const [pubkey] = await findProgramAddress(
    [
      Buffer.from(PARIMUTUEL_SEED),
      marketPubKey.toBuffer(),
      new Uint8Array(new BN(marketOpen).toArray('le', 8)),
      new Uint8Array(new BN(marketClose).toArray('le', 8)),
      new Uint8Array(new BN(timeWindowStart).toArray('le', 8)),
    ],
    programId
  );

  return new PublicKey(pubkey);
};

export const findNetworkProtocolFeeCollectorPubkey = async (
  networkWalletPubkey: PublicKey,
  protocolMintPubkey: PublicKey
): Promise<PublicKey> => {
  const [pubkey] = await findProgramAddress(
    [
      networkWalletPubkey.toBuffer(),
      TOKEN_PROGRAM_ID.toBuffer(),
      protocolMintPubkey.toBuffer(),
    ],
    ASSOCIATED_TOKEN_PROGRAM_ID
  );

  return new PublicKey(pubkey);
};

