// old method
export const calculateOdd = (position: number, total: number): string =>
  position && total ? Number(1 / (position / total)).toFixed(2) : "0";
// new method
export const calculateNetOdd = (position: number, total: number, rake: number): string =>
  position && total ? Number(total * (1.0 - rake) / position).toFixed(2) : "0";