import BN from 'bn.js';

export class TraderSequenceNumber {
  version: number;
  programId: string;
  bumpSeed: number;
  parimutuel: string;
  traderKey: string;
  sequenceNumber: BN;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    parimutuel: string;
    traderKey: string;
    sequenceNumber: BN;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.parimutuel = args.parimutuel;
    this.traderKey = args.traderKey;
    this.sequenceNumber = args.sequenceNumber;
  }
}
