import {
  Transaction,
  PublicKey,
  Keypair,
  sendAndConfirmTransaction,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID, Token } from '@solana/spl-token';

import { ParimutuelWeb3 } from '../web3';
import { createAssociatedTokenAccountInstruction } from '../instructions';
import { findAssociatedTokenAccountPubkey } from '../utils';

/**
 * send mint token
 * @param walletPubkey user wallet pubkey where token will send to
 * @param mintPubkey mint token pubkey
 * @param authorityKeypair keypair that owns mint token
 * @param amount amount to send
 * @returns void
 */
export async function sendTransferTokenTransaction(
  this: ParimutuelWeb3,
  walletPubkey: PublicKey,
  mintPubkey: PublicKey,
  authorityKeypair: Keypair,
  amount: number
): Promise<void> {
  const tradeTokenAccountPubkey = await findAssociatedTokenAccountPubkey(
    walletPubkey,
    mintPubkey
  );

  const traderTokenAccount = await this.connection.getAccountInfo(
    tradeTokenAccountPubkey
  );

  if (!traderTokenAccount) {
    const instruction = createAssociatedTokenAccountInstruction(
      tradeTokenAccountPubkey,
      authorityKeypair.publicKey,
      walletPubkey,
      mintPubkey
    );

    const transaction = new Transaction().add(instruction);

    const signature = await sendAndConfirmTransaction(
      this.connection,
      transaction,
      [authorityKeypair],
      { commitment: 'confirmed' }
    );

    if (!signature) {
      throw new Error('failed to create trader token account');
    }
  }

  const splToken = new Token(
    this.connection,
    mintPubkey,
    TOKEN_PROGRAM_ID,
    authorityKeypair
  );

  await splToken.mintTo(tradeTokenAccountPubkey, authorityKeypair, [], amount);
}
