import { AccountInfo, Connection, PublicKey } from '@solana/web3.js';
import _chunk from 'lodash/chunk';

import { Accounts } from '../types';
import { GET_MULTIPLE_ACCOUNTS_LIMIT } from '../constants';

const getMultipleAccountsCore = async (
  connection: Connection,
  pubkeys: PublicKey[]
): Promise<Accounts> => {
  const args = [pubkeys.map((key) => key.toBase58()), { commitment: 'recent' }];
  // @ts-ignore
  const res = await connection._rpcRequest('getMultipleAccounts', args);
  if (res.error) {
    throw new Error(
      'failed to get info about accounts ' +
        pubkeys.map((key) => key.toBase58()).join(', ') +
        ': ' +
        res.error.message
    );
  }
  if (!res.result) return [];

  const accounts: Array<null | AccountInfo<Buffer>> = res.result.value.map(
    // eslint-disable-next-line
    (account: AccountInfo<any>) => {
      if (!account) return null;

      const { executable, owner, lamports, data } = account;

      return {
        executable,
        owner: new PublicKey(owner),
        lamports,
        data: Buffer.from(data[0], 'base64'),
      };
    }
  );

  return accounts.map((account, idx) => {
    if (account === null) {
      return null;
    }
    return {
      pubkey: pubkeys[idx],
      account,
    };
  });
};

export const getMultipleAccounts = async (
  connection: Connection,
  pubkeys: PublicKey[]
): Promise<Accounts> => {
  if (pubkeys.length <= GET_MULTIPLE_ACCOUNTS_LIMIT) {
    const result = await getMultipleAccountsCore(connection, pubkeys);
    return result;
  } else {
    const batches = _chunk(pubkeys, GET_MULTIPLE_ACCOUNTS_LIMIT);
    const results = await Promise.all<Accounts>(
      batches.map((batch) => getMultipleAccountsCore(connection, batch))
    );
    return results.flat();
  }
};
