import {
  SystemProgram,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { serialize } from 'borsh';

import { SettlePositionArgs, schema } from '../borsh';

export const createSettlePositionInstruction = async (
  programId: PublicKey,
  settings: SettlePositionArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  protocolProductOraclePubkey: PublicKey,
  protocolPriceOraclePubkey: PublicKey,
  protocolMintPubkey: PublicKey,
  settlementMintPubkey: PublicKey,
  settlementTokenAccountPubkey: PublicKey,
  networkTraderFeePayerPubkey: PublicKey,
  networkTraderAccountPubkey: PublicKey,
  networkProtocolFeeCollectorPubkey: PublicKey,
  networkSettlementFeeCollectorPubkey: PublicKey,
  marketProtocolFeeCollectorPubkey: PublicKey,
  marketSettlementFeeCollectorPubkey: PublicKey,
  marketTraderAccountPubkey: PublicKey,
  traderPositionAccountPubkey: PublicKey,
  honeypotPubkey: PublicKey,
  parimutuelPubkey: PublicKey,
  parimutuelCreatorPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: parimutuelCreatorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: honeypotPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: false,
        isWritable: true, // TODO
      },
      {
        pubkey: networkTraderFeePayerPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: settlementTokenAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: networkTraderAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: marketTraderAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: traderPositionAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: protocolProductOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolPriceOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: settlementMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkProtocolFeeCollectorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: networkSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: marketProtocolFeeCollectorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: marketSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: TOKEN_PROGRAM_ID,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};
