import { PublicKey } from '@solana/web3.js';

import { AddressInfo } from '../types';

export const notEmpty = <T>(value: T): value is NonNullable<typeof value> =>
  !!value;

export const findProgramAddress = async (
  seeds: (Buffer | Uint8Array)[],
  programId: PublicKey
): Promise<AddressInfo> => {
  const key =
    'pda-' +
    seeds.reduce((acc, value) => acc + value.toString('hex'), '') +
    programId.toString();

  if (typeof window !== 'undefined') {
    const cached = localStorage?.getItem(key);
    if (cached) {
      const value = JSON.parse(cached);
      return [value.key, parseInt(value.nonce)] as [string, number];
    }
  }

  const result = await PublicKey.findProgramAddress(seeds, programId);

  if (typeof window !== 'undefined') {
    localStorage?.setItem(
      key,
      JSON.stringify({
        key: result[0].toBase58(),
        nonce: result[1],
      })
    );
  }

  return [result[0].toBase58(), result[1]];
};
