import {
  SYSVAR_CLOCK_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { serialize } from 'borsh';

import { UpdatePriceArgs, schema } from '../borsh';

export const createUpdatePriceInstruction = async (
  programId: PublicKey,
  settings: UpdatePriceArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  protocolProductOraclePubkey: PublicKey,
  protocolPriceOraclePubkey: PublicKey,
  settlementMintPubkey: PublicKey,
  networkSettlementFeeCollectorPubkey: PublicKey,
  honeypotPubkey: PublicKey,
  parimutuelPubkey: PublicKey,
  parimutuelCreatorPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolProductOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolPriceOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: parimutuelCreatorPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: honeypotPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: settlementMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: TOKEN_PROGRAM_ID,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_CLOCK_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};
