import {
  schema,
  FixedFees,
  LinearFees,
  Network,
  Store,
  Market,
  Parimutuel,
  TraderAccount,
  TraderSequenceNumber,
  TraderPositionAccount,
  TraderEntryAccount,
} from '../borsh';
import { PublicKey } from '@solana/web3.js';
import {
  AccountLayout,
  AccountInfo,
  MintInfo,
  MintLayout,
  u64,
} from '@solana/spl-token';
import { deserializeUnchecked } from 'borsh';

import { extendBorsh } from './borsh'; // need to extend borsh custom reader and writer

extendBorsh();

export const decodeAccount = (data: Buffer): AccountInfo => {
  const accountInfo = AccountLayout.decode(data);
  accountInfo.mint = new PublicKey(accountInfo.mint);
  accountInfo.owner = new PublicKey(accountInfo.owner);
  accountInfo.amount = u64.fromBuffer(accountInfo.amount);

  if (accountInfo.delegateOption === 0) {
    accountInfo.delegate = null;
    accountInfo.delegatedAmount = new u64(0);
  } else {
    accountInfo.delegate = new PublicKey(accountInfo.delegate);
    accountInfo.delegatedAmount = u64.fromBuffer(accountInfo.delegatedAmount);
  }

  accountInfo.isInitialized = accountInfo.state !== 0;
  accountInfo.isFrozen = accountInfo.state === 2;

  if (accountInfo.isNativeOption === 1) {
    accountInfo.rentExemptReserve = u64.fromBuffer(accountInfo.isNative);
    accountInfo.isNative = true;
  } else {
    accountInfo.rentExemptReserve = null;
    accountInfo.isNative = false;
  }

  if (accountInfo.closeAuthorityOption === 0) {
    accountInfo.closeAuthority = null;
  } else {
    accountInfo.closeAuthority = new PublicKey(accountInfo.closeAuthority);
  }

  return accountInfo;
};

export const decodeMint = (data: Buffer): MintInfo => {
  if (data.length !== MintLayout.span) {
    throw new Error('Not a valid Mint');
  }

  const mintInfo = MintLayout.decode(data);

  if (mintInfo.mintAuthorityOption === 0) {
    mintInfo.mintAuthority = null;
  } else {
    mintInfo.mintAuthority = new PublicKey(mintInfo.mintAuthority);
  }

  mintInfo.supply = u64.fromBuffer(mintInfo.supply);
  mintInfo.isInitialized = mintInfo.isInitialized !== 0;

  if (mintInfo.freezeAuthorityOption === 0) {
    mintInfo.freezeAuthority = null;
  } else {
    mintInfo.freezeAuthority = new PublicKey(mintInfo.freezeAuthority);
  }

  return mintInfo;
};

export const decodeFixedFees = (buffer: Buffer): FixedFees =>
  deserializeUnchecked(schema, FixedFees, buffer);

export const decodeLinearFees = (buffer: Buffer): LinearFees =>
  deserializeUnchecked(schema, LinearFees, buffer);

export const decodeNetwork = (buffer: Buffer): Network => {
  return deserializeUnchecked(schema, Network, buffer);
};

export const decodeStore = (buffer: Buffer): Store => {
  return deserializeUnchecked(schema, Store, buffer);
};

export const decodeMarket = (buffer: Buffer): Market => {
  return deserializeUnchecked(schema, Market, buffer);
};

export const decodeParimutuel = (buffer: Buffer): Parimutuel => {
  return deserializeUnchecked(schema, Parimutuel, buffer);
};

export const decodeTraderAccount = (buffer: Buffer): TraderAccount => {
  return deserializeUnchecked(schema, TraderAccount, buffer);
};

export const decodeTraderSequenceNumber = (
  buffer: Buffer
): TraderSequenceNumber => {
  return deserializeUnchecked(schema, TraderSequenceNumber, buffer);
};

export const decodeTraderPositionAccount = (
  buffer: Buffer
): TraderPositionAccount => {
  return deserializeUnchecked(schema, TraderPositionAccount, buffer);
};

export const decodeTraderEntryAccount = (
  buffer: Buffer
): TraderEntryAccount => {
  return deserializeUnchecked(schema, TraderEntryAccount, buffer);
};
