import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { serialize } from 'borsh';

import { WithdrawFeeArgs, schema } from '../borsh';

export const createWithdrawFeeInstruction = async (
  programId: PublicKey,
  settings: WithdrawFeeArgs,
  networkPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  protocolMintPubkey: PublicKey,
  protocolTokenAccountPubkey: PublicKey,
  networkTraderAccountPubkey: PublicKey,
  networkTraderFeePayerPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkTraderAccountPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkTraderFeePayerPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolTokenAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: protocolMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: TOKEN_PROGRAM_ID,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data,
  });
};
