import BN from 'bn.js';

import { LinearFees } from './LinearFees';

export class Parimutuel {
  version: number;
  programId: string;
  bumpSeed: number;
  strike: BN;
  index: BN;
  slot: BN;
  marketOpen: BN;
  marketClose: BN;
  timeWindowStart: BN;
  marketKey: string;
  honeypot: string;
  numPositions: BN;
  numPositionsSettled: BN;
  numPositionsDestroyed: BN;
  expired: boolean;
  rewardPerShare: BN;
  activeLongPositions: BN;
  activeShortPositions: BN;
  networkFees: LinearFees;
  marketFees: LinearFees;
  creator: string;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    strike: BN;
    index: BN;
    slot: BN;
    marketOpen: BN;
    marketClose: BN;
    timeWindowStart: BN;
    marketKey: string;
    honeypot: string;
    numPositions: BN;
    numPositionsSettled: BN;
    numPositionsDestroyed: BN;
    expired: boolean;
    rewardPerShare: BN;
    activeLongPositions: BN;
    activeShortPositions: BN;
    networkFees: LinearFees;
    marketFees: LinearFees;
    creator: string;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.strike = args.strike;
    this.index = args.index;
    this.slot = args.slot;
    this.marketOpen = args.marketOpen;
    this.marketClose = args.marketClose;
    this.timeWindowStart = args.timeWindowStart;
    this.marketKey = args.marketKey;
    this.honeypot = args.honeypot;
    this.numPositions = args.numPositions;
    this.numPositionsSettled = args.numPositionsSettled;
    this.numPositionsDestroyed = args.numPositionsDestroyed;
    this.expired = args.expired;
    this.rewardPerShare = args.rewardPerShare;
    this.activeLongPositions = args.activeLongPositions;
    this.activeShortPositions = args.activeShortPositions;
    this.networkFees = args.networkFees;
    this.marketFees = args.marketFees;
    this.creator = args.creator;
  }
}
