import { Connection, clusterApiUrl } from '@solana/web3.js';

import {
  sendAttachCookieTransaction,
  sendCreateParimutuelTransaction,
  sendCreateStoreTransaction,
  sendDepositFeeTransaction,
  sendPlacePositionTransaction,
  getPlacePositionInstruction,
  sendPlacePositionWithOperatorTransaction,
  sendSettlePositionTransaction,
  sendTransferTokenTransaction,
  sendUpdatePriceTransaction,
  sendWithdrawFeeTransaction,
  sendUpdateNetworkTransaction,
  sendDestroyParimutuelTransaction,
  sendDestroyPositionTransaction,
  sendDestroyPositionEntryTransaction,
} from '../transactions';
import {
  getFees,
  getMarkets,
  getNetwork,
  getStore,
  getNetworkTraderAccount,
  getParimutuel,
  getParimutuels,
  getUserPositions,
  getParimutuelPositions,
  getTraderFeePayerAccount,
  getEntries,
} from '../queries';

import { ParimutuelConfig } from './config';
import { ParimutuelWeb3Interface } from './interface';

export class ParimutuelWeb3 implements ParimutuelWeb3Interface {
  config: ParimutuelConfig;
  connection: Connection;

  constructor(config: ParimutuelConfig, connection?: Connection) {
    this.config = config;
    this.connection = new Connection(
      config.SOLANA_CLUSTER_URL ?? clusterApiUrl('devnet'),
      'recent'
    );

    if (connection) {
      this.connection = connection;
    }
  }

  updateNetwork = sendUpdateNetworkTransaction.bind(this);

  createParimutuel = sendCreateParimutuelTransaction.bind(this);

  updatePrice = sendUpdatePriceTransaction.bind(this);

  placePosition = sendPlacePositionTransaction.bind(this);

  getPlacePositionInstruction = getPlacePositionInstruction.bind(this);

  placePositionWithOperator = sendPlacePositionWithOperatorTransaction.bind(this);

  settlePosition = sendSettlePositionTransaction.bind(this);

  destroyParimutuel = sendDestroyParimutuelTransaction.bind(this);

  destroyPosition = sendDestroyPositionTransaction.bind(this);

  destroyPositionEntry = sendDestroyPositionEntryTransaction.bind(this);

  transferToken = sendTransferTokenTransaction.bind(this);

  depositFee = sendDepositFeeTransaction.bind(this);

  withdrawFee = sendWithdrawFeeTransaction.bind(this);

  createStore = sendCreateStoreTransaction.bind(this);

  attachCookie = sendAttachCookieTransaction.bind(this);

  getFees = getFees.bind(this);

  getMarkets = getMarkets.bind(this);

  getNetwork = getNetwork.bind(this);

  getNetworkTraderAccount = getNetworkTraderAccount.bind(this);

  getParimutuel = getParimutuel.bind(this);

  getParimutuels = getParimutuels.bind(this);

  getUserPositions = getUserPositions.bind(this);

  getParimutuelPositions = getParimutuelPositions.bind(this);

  getEntries = getEntries.bind(this);

  getStore = getStore.bind(this);

  getTraderFeePayerAccount = getTraderFeePayerAccount.bind(this);
}
