import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { serialize } from 'borsh';

import { ParimutuelArgs, schema } from '../borsh';

export const createParimutuelInstruction = async (
  programId: PublicKey,
  settings: ParimutuelArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  settlementMintPubkey: PublicKey,
  protocolProductOraclePubkey: PublicKey,
  protocolPriceOraclePubkey: PublicKey,
  honeypotPubkey: PublicKey,
  parimutuelPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));
  const payerPubkey = userWalletPubkey;

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolProductOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolPriceOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: settlementMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: honeypotPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: TOKEN_PROGRAM_ID,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data,
  });
};
