import { TransactionInstruction, PublicKey } from '@solana/web3.js';
import { serialize } from 'borsh';

import { DestroyPositionEntryArgs, schema } from '../borsh';

export const createDestroyPositionEntryInstruction = async (
  programId: PublicKey,
  settings: DestroyPositionEntryArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  traderPositionAccountPubkey: PublicKey,
  entryAccountPubkey: PublicKey,
  parimutuelPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: traderPositionAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: entryAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
    ],
    programId: programId,
    data: data,
  });
};
