import { TransactionInstruction, Keypair, PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { StoreArgs, FixedFees, Version } from '../borsh';
import {
  createStoreInstruction,
  createAssociatedTokenAccountInstruction,
} from '../instructions';
import {
  findStorePubkey,
  findAssociatedTokenAccountPubkey,
  sendTransaction,
  decodeNetwork,
} from '../utils';
import { WalletSigner } from '../types';

export async function sendCreateStoreTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair,
  storeWalletPubkey: PublicKey,
  marketPubkey: PublicKey,
  protocolFeeBps: number,
  settlementFeeBps: number
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');

  const instructions: TransactionInstruction[] = [];
  const signers: Keypair[] = [];
  const { PARIMUTUEL_PROGRAM_ID, NETWORK } = this.config;
  const userWalletPubkey = userWallet.publicKey;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const settings = new StoreArgs({
    fees: new FixedFees({
      version: Version.FixedFeesV1,
      protocolFeeBps,
      settlementFeeBps,
    }),
  });

  const networkPubkey = NETWORK;
  const networkAccount = await this.connection.getAccountInfo(networkPubkey);
  if (!networkAccount) {
    throw new Error('no network account');
  }

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));
  const protocolMintPubkey = new PublicKey(decodedNetworkAccount.protocolToken);
  const settlementMintPubkey = new PublicKey(
    decodedNetworkAccount.settlementToken
  );

  const storePubkey = await findStorePubkey(
    PARIMUTUEL_PROGRAM_ID,
    marketPubkey,
    storeWalletPubkey
  );

  const storeProtocolFeeCollectorPubkey =
    await findAssociatedTokenAccountPubkey(
      storeWalletPubkey,
      protocolMintPubkey
    );

  const storeSettlementFeeCollectorPubkey =
    await findAssociatedTokenAccountPubkey(
      storeWalletPubkey,
      settlementMintPubkey
    );

  const [protocolFeeCollectorAccount, settlementFeeCollectorAccount] =
    await this.connection.getMultipleAccountsInfo([
      storeProtocolFeeCollectorPubkey,
      storeSettlementFeeCollectorPubkey,
    ]);

  if (!protocolFeeCollectorAccount) {
    const instruction = createAssociatedTokenAccountInstruction(
      storeProtocolFeeCollectorPubkey,
      storeWalletPubkey,
      storeWalletPubkey,
      protocolMintPubkey
    );
    instructions.push(instruction);
  }

  if (!settlementFeeCollectorAccount) {
    const instruction = createAssociatedTokenAccountInstruction(
      storeSettlementFeeCollectorPubkey,
      storeWalletPubkey,
      storeWalletPubkey,
      settlementMintPubkey
    );
    instructions.push(instruction);
  }

  const storeInstruction = await createStoreInstruction(
    PARIMUTUEL_PROGRAM_ID,
    settings,
    networkPubkey,
    marketPubkey,
    storeWalletPubkey,
    userWalletPubkey,
    storePubkey,
    storeProtocolFeeCollectorPubkey,
    storeSettlementFeeCollectorPubkey
  );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [...instructions, storeInstruction],
    signers,
    'max',
    signers.length > 0
  );

  if (!response) {
    throw new Error('failed to create store');
  } else {
    return response.transactionId;
  }
}
