import {
  SYSVAR_CLOCK_PUBKEY,
  TransactionInstruction,
  PublicKey,
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
} from '@solana/web3.js';
import { TOKEN_PROGRAM_ID } from '@solana/spl-token';
import { serialize } from 'borsh';

import { UpdatePositionArgs, schema } from '../borsh';

export const createPlacePositionInstruction = async (
  programId: PublicKey,
  settings: UpdatePositionArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  userWalletPubkey: PublicKey,
  settlementTokenAccountPubkey: PublicKey,
  traderPositionAccountPubkey: PublicKey,
  entryAccountPubkey: PublicKey,
  honeypotPubkey: PublicKey,
  parimutuelPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: parimutuelPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: honeypotPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: true,
        isWritable: true,
      },
      {
        pubkey: settlementTokenAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: traderPositionAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: entryAccountPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: userWalletPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: TOKEN_PROGRAM_ID,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_CLOCK_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data,
  });
};
