import { LinearFees } from './LinearFees';

export class Network {
  version: number;
  programId: string;
  bumpSeed: number;
  authority: string;
  protocolToken: string;
  settlementToken: string;
  protocolProductOracle: string;
  protocolPriceOracle: string;
  protocolFeeCollector: string;
  settlementFeeCollector: string;
  fee: LinearFees;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    authority: string;
    protocolToken: string;
    settlementToken: string;
    protocolProductOracle: string;
    protocolPriceOracle: string;
    protocolFeeCollector: string;
    settlementFeeCollector: string;
    fee: LinearFees;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.authority = args.authority;
    this.protocolToken = args.protocolToken;
    this.settlementToken = args.settlementToken;
    this.protocolProductOracle = args.protocolProductOracle;
    this.protocolPriceOracle = args.protocolPriceOracle;
    this.protocolFeeCollector = args.protocolFeeCollector;
    this.settlementFeeCollector = args.settlementFeeCollector;
    this.fee = args.fee;
  }
}
