import { getMarketPubkeys } from './../utils/parimutuel';

import { ParimutuelWeb3 } from '../web3';
import { decodeFixedFees, decodeMarket } from '../utils';
import { MarketPairEnum, ParimutuelMarket } from '../types';
import { notEmpty } from '../utils';

export async function getMarkets(
  this: ParimutuelWeb3,
  marketPair: MarketPairEnum
): Promise<ParimutuelMarket[]> {
  const markets = getMarketPubkeys(this.config, marketPair);
  const marketPubkeys = markets.map((market) => market.pubkey);

  const marketAccounts = await this.connection.getMultipleAccountsInfo(
    marketPubkeys
  );

  return marketAccounts
    .map((account, index) => {
      if (!account) return null;

      const data = Buffer.from(account.data);
      return {
        pubkey: marketPubkeys[index],
        account: account,
        info: {
          market: decodeMarket(data),
          fixedFees: decodeFixedFees(data.slice(-65)),
        },
      };
    })
    .filter(notEmpty);
}
