import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { serialize } from 'borsh';

import { MarketArgs, schema } from '../borsh';

export const createMarketInstruction = async (
  programId: PublicKey,
  settings: MarketArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  marketWalletPubkey: PublicKey,
  protocolProductOraclePubkey: PublicKey,
  protocolPriceOraclePubkey: PublicKey,
  marketProtocolFeeCollectorPubkey: PublicKey,
  marketSettlementFeeCollectorPubkey: PublicKey,
  payerPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: protocolProductOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolPriceOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketWalletPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: marketProtocolFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};
