import { PublicKey, Keypair } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { NetworkTraderFeePayerArgs } from '../borsh';
import { createNetworkTraderFeePayerInstruction } from '../instructions';
import {
  findNetworkTraderAccountPubkey,
  findNetworkTraderFeePayerPubkey,
  decodeNetwork,
  sendTransaction,
} from '../utils';
import { WalletSigner } from '../types';

export async function sendCreateNetworkTraderFeePayerTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');

  const signers: Keypair[] = [];
  const { PARIMUTUEL_PROGRAM_ID, NETWORK } = this.config;
  const userWalletPubkey = userWallet.publicKey;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const networkPubkey = NETWORK;

  const networkAccount = await this.connection.getAccountInfo(networkPubkey);
  if (!networkAccount) {
    throw new Error('no network account'); // TODO: centralize error
  }

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));
  const protocolMintPubkey = new PublicKey(decodedNetworkAccount.protocolToken);

  const networkTraderAccountPubkey = await findNetworkTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    networkPubkey
  );

  const networkTraderFeePayerPubkey = await findNetworkTraderFeePayerPubkey(
    PARIMUTUEL_PROGRAM_ID,
    networkTraderAccountPubkey
  );

  const networkTraderFeePayerInstruction =
    await createNetworkTraderFeePayerInstruction(
      PARIMUTUEL_PROGRAM_ID,
      new NetworkTraderFeePayerArgs(),
      networkPubkey,
      userWallet.publicKey,
      protocolMintPubkey,
      networkTraderAccountPubkey,
      networkTraderFeePayerPubkey
    );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [networkTraderFeePayerInstruction],
    signers,
    'max',
    signers.length > 0
  );

  if (!response) {
    throw new Error('failed to create fee payer account');
  } else {
    return response.transactionId;
  }
}
