import BN from 'bn.js';
import { Keypair, PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { NetworkUpdateArgs } from '../borsh';
import { WalletSigner } from '../types';
import { updateNetworkInstruction } from '../instructions';
import { TwoTieredFees } from '../borsh'
import {
  decodeNetwork,
  sendTransaction,
} from '../utils';

/**
 * update network transaction
 * @param userWallet user wallet
 * @param protocolMintPubkey: protocol mint address
 * @param settlementMintPubkey settlement mint address
 * @param protocolProductOraclePubkey: protocol product oracle address
 * @param protocolPriceOraclePubkey: protocol price oracle address
 * @param networkProtocolFeeCollectorPubkey: network protocol fee collector address
 * @param networkSettlementFeeCollectorPubkey: network settlement fee collector address
 * @param protocolTokenRequiredForMinFee: min protocol token required for reduced fee workflow
 * @param minProtocolFeeBps: min protocol fee bps in Tiered and Linear Fee structures
 * @param maxProtocolFeeBps: max protocol fee bps in Tiered and Linear Fee structures
 * @param minSettlementFeeBps: min settlement fee bps in Tiered and Linear Fee structures
 * @param maxSettlementFeeBps: min settlement fee bps in Tiered and Linear Fee structures
 * @returns transaction id
 */
export async function sendUpdateNetworkTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair,
  protocolMintPubkey: PublicKey | unknown,
  settlementMintPubkey: PublicKey | unknown,
  protocolProductOraclePubkey: PublicKey | unknown,
  protocolPriceOraclePubkey: PublicKey | unknown,
  networkProtocolFeeCollectorPubkey: PublicKey | unknown,
  networkSettlementFeeCollectorPubkey: PublicKey | unknown,
  protocolTokenRequiredForMinFee: BN = new BN(1000000),
  minProtocolFeeBps: number = 10,
  maxProtocolFeeBps: number = 150,
  minSettlementFeeBps: number = 100,
  maxSettlementFeeBps: number = 300,
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');
  const signers: Keypair[] = [];
  const { PARIMUTUEL_PROGRAM_ID, NETWORK } = this.config;
  const userWalletPubkey = userWallet.publicKey;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const networkSettings = new NetworkUpdateArgs({
    fees: new TwoTieredFees({
      version: 1,
      protocolTokenRequiredForMinFee,
      minProtocolFeeBps,
      maxProtocolFeeBps,
      minSettlementFeeBps,
      maxSettlementFeeBps,
    }),
  });

  const networkPubkey = NETWORK;

  const [networkAccount] = await this.connection.getMultipleAccountsInfo([
    networkPubkey,
  ]);

  if (!networkAccount) {
    throw new Error('no network or market account');
  }

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));

  const ix = await updateNetworkInstruction(
    PARIMUTUEL_PROGRAM_ID,
    networkSettings,
    networkPubkey,
    userWalletPubkey, // User must be network authority
    protocolProductOraclePubkey as PublicKey || new PublicKey(decodedNetworkAccount.protocolProductOracle),
    protocolPriceOraclePubkey as PublicKey || new PublicKey(decodedNetworkAccount.protocolPriceOracle),
    networkProtocolFeeCollectorPubkey as PublicKey || new PublicKey(decodedNetworkAccount.protocolFeeCollector),
    networkSettlementFeeCollectorPubkey as PublicKey || new PublicKey(decodedNetworkAccount.settlementFeeCollector),
    protocolMintPubkey as PublicKey || new PublicKey(decodedNetworkAccount.protocolToken),
    settlementMintPubkey as PublicKey || new PublicKey(decodedNetworkAccount.settlementToken)
  );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [ix],
    signers,
    'max',
    signers.length > 0,
    false
  );

  if (!response) {
    throw new Error(
      `failed to update network: ${networkPubkey.toBase58()}`
    );
  } else {
    return response.transactionId;
  }
}
