import BN from 'bn.js';

export class TraderEntryAccount {
  version: number;
  programId: string;
  bumpSeed: number;
  traderPosition: string;
  sequenceNumber: BN;
  traderWallet: string;
  direction: number;
  amount: BN;
  timestamp: BN;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    traderPosition: string;
    sequenceNumber: BN;
    traderWallet: string;
    direction: number;
    amount: BN;
    timestamp: BN;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.traderPosition = args.traderPosition;
    this.sequenceNumber = args.sequenceNumber;
    this.traderWallet = args.traderWallet;
    this.direction = args.direction;
    this.amount = args.amount;
    this.timestamp = args.timestamp;
  }
}
