import {
  PublicKey,
  GetProgramAccountsConfig,
  MemcmpFilter,
  DataSizeFilter,
} from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import {
  decodeTraderEntryAccount,
  decodeTraderPositionAccount,
  getMultipleAccounts,
} from '../utils';
import { ParimutuelEntry } from '../types';

export async function getEntries(
  this: ParimutuelWeb3,
  traderPositionPubkey: PublicKey
): Promise<ParimutuelEntry[]> {
  const { PARIMUTUEL_PROGRAM_ID } = this.config;

  const traderEntryAccounts = await this.connection.getProgramAccounts(
    PARIMUTUEL_PROGRAM_ID,
    {
      filters: [
        { dataSize: 123 } as DataSizeFilter,
        {
          memcmp: {
            offset: 34,
            bytes: traderPositionPubkey?.toBase58(),
          },
        } as MemcmpFilter,
      ],
    } as GetProgramAccountsConfig
  );

  const decodedEntryAccounts = traderEntryAccounts.map((account) =>
    decodeTraderEntryAccount(account.account.data)
  );

  const traderPositionPubkeys = decodedEntryAccounts.map(
    (account) => new PublicKey(account.traderPosition)
  );

  const traderPositionAccounts = await getMultipleAccounts(
    this.connection,
    traderPositionPubkeys
  );

  const decodedPositionAccounts = traderPositionAccounts.map((account) => {
    if (account) {
      return decodeTraderPositionAccount(account.account.data);
    } else return null;
  });

  const entries = traderEntryAccounts.map((account, index) => {
    const positionAccount = decodedPositionAccounts[index];

    return {
      pubkey: account.pubkey,
      account: account.account,
      info: {
        parimutuelPubkey: new PublicKey(
          positionAccount !== null ? positionAccount.parimutuel : ''
        ),
        entry: decodedEntryAccounts[index],
      },
    };
  });

  return entries;
}
