import { Keypair, PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { ParimutuelArgs } from '../borsh';
import { WalletSigner } from '../types';
import { createParimutuelInstruction } from '../instructions';
import {
  findParimutuelPubkey,
  findHoneypotPubkey,
  decodeNetwork,
  decodeMarket,
  sendTransaction,
} from '../utils';

/**
 * create parimutuel transaction
 * @param userWallet user wallet
 * @param marketPubkey market pubkey where parimutuel will be created
 * @param marketOpen parimutuel creation time
 * @param timeWindowStart parimutuel start time
 * @param marketClose parimutuel end time
 * @returns transaction id
 */
export async function sendCreateParimutuelTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair,
  marketPubkey: PublicKey,
  marketOpen: number,
  timeWindowStart: number,
  marketClose: number
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');
  const signers: Keypair[] = [];
  const { PARIMUTUEL_PROGRAM_ID, NETWORK } = this.config;
  const userWalletPubkey = userWallet.publicKey;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const parimutuelSettings = new ParimutuelArgs({
    marketOpen: marketOpen,
    marketClose: marketClose,
    timeWindowStart: timeWindowStart,
  });

  const networkPubkey = NETWORK;

  const [networkAccount, marketAccount] =
    await this.connection.getMultipleAccountsInfo([
      networkPubkey,
      marketPubkey,
    ]);

  if (!networkAccount || !marketAccount) {
    throw new Error('no network or market account');
  }

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));
  const decodeMarketAccount = decodeMarket(Buffer.from(marketAccount.data));

  const settlementMintPubkey = new PublicKey(
    decodedNetworkAccount.settlementToken
  );
  const protocolProductOraclePubkey = new PublicKey(
    decodeMarketAccount.underlyingProductOracle
  );
  const protocolPriceOraclePubkey = new PublicKey(
    decodeMarketAccount.underlyingPriceOracle
  );

  const parimutuelPubkey = await findParimutuelPubkey(
    PARIMUTUEL_PROGRAM_ID,
    marketPubkey,
    marketOpen,
    timeWindowStart,
    marketClose
  );

  const honeypotPubkey = await findHoneypotPubkey(
    PARIMUTUEL_PROGRAM_ID,
    parimutuelPubkey
  );

  const createInstruction = await createParimutuelInstruction(
    PARIMUTUEL_PROGRAM_ID,
    parimutuelSettings,
    networkPubkey,
    marketPubkey,
    userWalletPubkey,
    settlementMintPubkey,
    protocolProductOraclePubkey,
    protocolPriceOraclePubkey,
    honeypotPubkey,
    parimutuelPubkey
  );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [createInstruction],
    signers,
    'max',
    signers.length > 0,
    false,
    false
  );

  if (!response) {
    throw new Error(
      `failed to create parimutuel: ${parimutuelPubkey.toBase58()}`
    );
  } else {
    return response.transactionId;
  }
}
