import { PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { decodeTraderAccount, findNetworkTraderAccountPubkey } from '../utils';
import { ParimutuelTraderAccount } from '../types';

export async function getNetworkTraderAccount(
  this: ParimutuelWeb3,
  userWalletPubkey: PublicKey,
  networkPubkey: PublicKey
): Promise<ParimutuelTraderAccount> {
  const { PARIMUTUEL_PROGRAM_ID } = this.config;
  const networkTraderAccountPubkey = await findNetworkTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    networkPubkey
  );

  const networkTraderAccount = await this.connection.getAccountInfo(
    networkTraderAccountPubkey
  );

  if (!networkTraderAccount) {
    throw new Error('no trader account');
  }

  return {
    pubkey: networkTraderAccountPubkey,
    account: networkTraderAccount,
    info: {
      traderAccount: decodeTraderAccount(networkTraderAccount?.data),
    },
  };
}
