import { FixedFees } from './FixedFees';

export class Store {
  version: number;
  programId: string;
  bumpSeed: number;
  marketKey: string;
  wallet: string;
  protocolFeeCollector: string;
  settlementFeeCollector: string;
  fee: FixedFees;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    marketKey: string;
    wallet: string;
    protocolFeeCollector: string;
    settlementFeeCollector: string;
    fee: FixedFees;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.marketKey = args.marketKey;
    this.wallet = args.wallet;
    this.protocolFeeCollector = args.protocolFeeCollector;
    this.settlementFeeCollector = args.settlementFeeCollector;
    this.fee = args.fee;
  }
}
