import moment from 'moment';
import { ParimutuelTime } from '../types';

export const getHistoricalTimes = (
  marketPubkey: string,
  duration: number,
  number: number
): ParimutuelTime[] => {
  const currentTime = new Date().getTime();
  const elapsed = currentTime % duration;
  const startTime = currentTime - elapsed;

  const times: ParimutuelTime[] = Array.from(Array(number).keys()).map(
    (num) => {
      return { marketPubkey, startTime: startTime - num * duration, duration };
    }
  );

  return times.reverse();
};

export const getUpcomingTimes = (
  marketPubkey: string,
  duration: number,
  number: number
): ParimutuelTime[] => {
  const currentTime = new Date().getTime();
  const elapsed = currentTime % duration;
  const startTime = currentTime - elapsed;

  const times: ParimutuelTime[] = Array.from(Array(number).keys()).map(
    (num) => {
      return { marketPubkey, startTime: startTime + num * duration, duration };
    }
  );

  return times.reverse();
};

export const formatDuration = (duration: number): string => {
  const hours = Math.floor(duration / (60 * 60));
  const minutes = Math.floor((duration / 60) % 60);
  const seconds = Math.floor(duration % 60);

  if (hours === 24) return '1 day';
  if (hours === 1) return '1 hour';
  if (minutes === 15) return '15 min';
  if (minutes === 5) return '5 min';
  if (seconds === 60) return '1 min';
  return '1 MIN';
};

export const formatTime = (unixTime: number): string =>
  moment.unix(unixTime / 1000).format('MM/DD/YYYY hh:mm:ss');

export const formatDate = (unixTime: number): string =>
  moment.unix(unixTime / 1000).format('MM/DD/YYYY');

export const formatHour = (unixTime: number): string =>
  moment.unix(unixTime / 1000).format('hh:mm:ss');
