import BN from 'bn.js';

export class TraderPositionAccount {
  version: number;
  programId: string;
  bumpSeed: number;
  sequenceNumber: BN;
  parimutuel: string;
  traderWallet: string;
  hasStore: boolean;
  store: string;
  longPosition: BN;
  shortPosition: BN;
  numEntries: BN;
  paid: boolean;
  payout: BN;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    sequenceNumber: BN;
    parimutuel: string;
    traderWallet: string;
    hasStore: boolean;
    store: string;
    longPosition: BN;
    shortPosition: BN;
    numEntries: BN;
    paid: boolean;
    payout: BN;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.sequenceNumber = args.sequenceNumber;
    this.parimutuel = args.parimutuel;
    this.traderWallet = args.traderWallet;
    this.hasStore = args.hasStore;
    this.store = args.store;
    this.longPosition = args.longPosition;
    this.shortPosition = args.shortPosition;
    this.numEntries = args.numEntries;
    this.paid = args.paid;
    this.payout = args.payout;
  }
}
