import { Keypair, PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { SettlePositionArgs } from '../borsh';
import { createSettlePositionInstruction } from '../instructions';
import {
  findNetworkProtocolFeeCollectorPubkey,
  findNetworkSettlementFeeCollectorPubkey,
  findMarketProtocolFeeCollectorPubkey,
  findMarketSettlementFeeCollectorPubkey,
  findAssociatedTokenAccountPubkey,
  findHoneypotPubkey,
  findNetworkTraderAccountPubkey,
  findMarketTraderAccountPubkey,
  findNetworkTraderFeePayerPubkey,
  decodeNetwork,
  decodeParimutuel,
  decodeMarket,
  findTraderPositionAccountPubkey,
  sendTransaction,
} from '../utils';
import { WalletSigner } from '../types';

export async function sendSettlePositionTransaction(
  this: ParimutuelWeb3,
  userWallet: WalletSigner | Keypair,
  traderWalletPubkey: PublicKey,
  parimutuelPubkey: PublicKey
): Promise<string> {
  if (!userWallet.publicKey) throw new Error('user wallet not connected');

  const signers: Keypair[] = [];
  const {
    NETWORK,
    PARIMUTUEL_PROGRAM_ID,
    NETWORK_PROTOCOL_FEE_COLLECTOR,
    MARKET_PROTOCOL_FEE_COLLECTOR,
  } = this.config;

  // eslint-disable-next-line no-prototype-builtins
  if (userWallet?.hasOwnProperty('_keypair')) {
    signers.push(userWallet as Keypair);
  }

  const parimutuelAccount = await this.connection.getAccountInfo(
    parimutuelPubkey
  );
  if (!parimutuelAccount) {
    throw new Error('no parimutuel account');
  }

  const decodedParimutuelAccount = decodeParimutuel(
    Buffer.from(parimutuelAccount.data)
  );
  const marketPubkey = new PublicKey(decodedParimutuelAccount.marketKey);
  const parimutuelCreatorPubkey = new PublicKey(decodedParimutuelAccount.creator);

  const networkPubkey = NETWORK;

  const [networkAccount, marketAccount] =
    await this.connection.getMultipleAccountsInfo([
      networkPubkey,
      marketPubkey,
    ]);
  if (!networkAccount) {
    throw new Error('no network account');
  }
  if (!marketAccount) {
    throw new Error('no market account');
  }

  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));
  const decodeMarketAccount = decodeMarket(Buffer.from(marketAccount.data));

  const protocolMintPubkey = new PublicKey(decodedNetworkAccount.protocolToken);
  const settlementMintPubkey = new PublicKey(
    decodedNetworkAccount.settlementToken
  );
  const networkWalletPubkey = new PublicKey(decodedNetworkAccount.authority);

  const networkProtocolFeeCollectorPubkey =
    await findNetworkProtocolFeeCollectorPubkey(
      networkWalletPubkey,
      protocolMintPubkey
    );

  const networkSettlementFeeCollectorPubkey =
    await findNetworkSettlementFeeCollectorPubkey(
      networkWalletPubkey,
      settlementMintPubkey
    );

  const marketWalletPubkey = new PublicKey(decodeMarketAccount.authority);
  const protocolProductOraclePubkey = new PublicKey(
    decodeMarketAccount.underlyingProductOracle
  );
  const protocolPriceOraclePubkey = new PublicKey(
    decodeMarketAccount.underlyingPriceOracle
  );

  const marketProtocolFeeCollectorPubkey =
    await findMarketProtocolFeeCollectorPubkey(
      marketWalletPubkey,
      protocolMintPubkey
    );

  const marketSettlementFeeCollectorPubkey =
    await findMarketSettlementFeeCollectorPubkey(
      marketWalletPubkey,
      settlementMintPubkey
    );

  const protocolTokenAccountPubkey = await findAssociatedTokenAccountPubkey(
    traderWalletPubkey,
    protocolMintPubkey
  );

  const settlementTokenAccountPubkey = await findAssociatedTokenAccountPubkey(
    traderWalletPubkey,
    settlementMintPubkey
  );

  const traderPositionAccountPubkey = await findTraderPositionAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    traderWalletPubkey,
    parimutuelPubkey
  );

  const [
    networkProtocolFeeCollectorAccount,
    networkSettlementFeeCollectorAccount,
    marketProtocolFeeCollectorAccount,
    marketSettlementFeeCollectorAccount,
    protocolTokenAccount,
    settlementTokenAccount,
    positionAccount,
  ] = await this.connection.getMultipleAccountsInfo([
    NETWORK_PROTOCOL_FEE_COLLECTOR ?? networkProtocolFeeCollectorPubkey,
    networkSettlementFeeCollectorPubkey,
    MARKET_PROTOCOL_FEE_COLLECTOR ?? marketProtocolFeeCollectorPubkey,
    marketSettlementFeeCollectorPubkey,
    protocolTokenAccountPubkey,
    settlementTokenAccountPubkey,
    traderPositionAccountPubkey,
  ]);

  if (!networkProtocolFeeCollectorAccount) {
    throw new Error('no network protocol account');
  }

  if (!networkSettlementFeeCollectorAccount) {
    throw new Error('no network settlement account');
  }

  if (!marketProtocolFeeCollectorAccount) {
    throw new Error('no market protocol account');
  }

  if (!marketSettlementFeeCollectorAccount) {
    throw new Error('no market settlement account');
  }

  if (!protocolTokenAccount) {
    throw new Error('no protocol token account');
  }

  if (!settlementTokenAccount) {
    throw new Error('no settlement token account');
  }

  if (!positionAccount) {
    throw new Error('no position account');
  }

  const honeypotPubkey = await findHoneypotPubkey(
    PARIMUTUEL_PROGRAM_ID,
    parimutuelPubkey
  );

  const networkTraderAccountPubkey = await findNetworkTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    traderWalletPubkey,
    networkPubkey
  );

  const marketTraderAccountPubkey = await findMarketTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    traderWalletPubkey,
    marketPubkey
  );

  const networkTraderFeePayerPubkey = await findNetworkTraderFeePayerPubkey(
    PARIMUTUEL_PROGRAM_ID,
    networkTraderAccountPubkey
  );

  const instruction = await createSettlePositionInstruction(
    PARIMUTUEL_PROGRAM_ID,
    new SettlePositionArgs(),
    networkPubkey,
    marketPubkey,
    traderWalletPubkey,
    protocolProductOraclePubkey,
    protocolPriceOraclePubkey,
    protocolMintPubkey,
    settlementMintPubkey,
    settlementTokenAccountPubkey,
    networkTraderFeePayerPubkey,
    networkTraderAccountPubkey,
    NETWORK_PROTOCOL_FEE_COLLECTOR ?? networkProtocolFeeCollectorPubkey,
    networkSettlementFeeCollectorPubkey,
    MARKET_PROTOCOL_FEE_COLLECTOR ?? marketProtocolFeeCollectorPubkey,
    marketSettlementFeeCollectorPubkey,
    marketTraderAccountPubkey,
    traderPositionAccountPubkey,
    honeypotPubkey,
    parimutuelPubkey,
    parimutuelCreatorPubkey
  );

  const response = await sendTransaction(
    this.connection,
    userWallet as WalletSigner,
    [instruction],
    signers,
    'max',
    signers.length > 0,
    false,
    false
  );

  if (!response) {
    throw new Error('failed to settle position');
  } else {
    return response.transactionId;
  }
}
