import {
  PublicKey,
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
} from '@solana/web3.js';
import {
  ASSOCIATED_TOKEN_PROGRAM_ID,
  TOKEN_PROGRAM_ID,
} from '@solana/spl-token';

export const createAssociatedTokenAccountInstruction = (
  associatedTokenPubkey: PublicKey,
  payerPubkey: PublicKey,
  walletPubkey: PublicKey,
  splTokenMintPubkey: PublicKey
): TransactionInstruction => {
  const keys = [
    {
      pubkey: payerPubkey,
      isSigner: true,
      isWritable: true,
    },
    {
      pubkey: associatedTokenPubkey,
      isSigner: false,
      isWritable: true,
    },
    {
      pubkey: walletPubkey,
      isSigner: false,
      isWritable: false,
    },
    {
      pubkey: splTokenMintPubkey,
      isSigner: false,
      isWritable: false,
    },
    {
      pubkey: SystemProgram.programId,
      isSigner: false,
      isWritable: false,
    },
    {
      pubkey: TOKEN_PROGRAM_ID,
      isSigner: false,
      isWritable: false,
    },
    {
      pubkey: SYSVAR_RENT_PUBKEY,
      isSigner: false,
      isWritable: false,
    },
  ];

  return new TransactionInstruction({
    keys,
    programId: ASSOCIATED_TOKEN_PROGRAM_ID,
    data: Buffer.from([]),
  });
};
