import { ParimutuelWeb3 } from '../web3';
import { decodeLinearFees, decodeNetwork } from '../utils';
import { ParimutuelNetwork } from '../types';

export async function getNetwork(
  this: ParimutuelWeb3
): Promise<ParimutuelNetwork> {
  const { NETWORK } = this.config;
  const networkPubkey = NETWORK;
  const networkAccount = await this.connection.getAccountInfo(networkPubkey);
  if (!networkAccount) {
    throw new Error('Failed to find network account');
  }

  const data = Buffer.from(networkAccount.data);
  return {
    pubkey: networkPubkey,
    account: networkAccount,
    info: {
      network: decodeNetwork(data),
      linearFees: decodeLinearFees(data.slice(-65)),
    },
  };
}
