import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { serialize } from 'borsh';

import { StoreArgs, schema } from '../borsh';

export const createStoreInstruction = async (
  programId: PublicKey,
  settings: StoreArgs,
  networkPubkey: PublicKey,
  marketPubkey: PublicKey,
  storeWalletPubkey: PublicKey,
  payerPubkey: PublicKey,
  storePubkey: PublicKey,
  storeProtocolFeeCollectorPubkey: PublicKey,
  storeSettlementFeeCollectorPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: marketPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: storePubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: storeWalletPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: storeProtocolFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: storeSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data,
  });
};
