import { PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import { ParimutuelFees } from '../types';
import { decodeAccount } from '../utils';
import {
  findMarketProtocolFeeCollectorPubkey,
  findMarketSettlementFeeCollectorPubkey,
  findNetworkProtocolFeeCollectorPubkey,
  findNetworkSettlementFeeCollectorPubkey,
  decodeNetwork,
} from '../utils';

export async function getFees(
  this: ParimutuelWeb3,
  networkWalletPubkey: PublicKey,
  marketWalletPubkey: PublicKey
): Promise<ParimutuelFees> {
  let networkProtocolFeeAmount = 0;
  let networkSettlementFeeAmount = 0;
  let marketProtocolFeeAmount = 0;
  let marketSettlementFeeAmount = 0;

  const {
    NETWORK,
    NETWORK_PROTOCOL_FEE_COLLECTOR,
    MARKET_PROTOCOL_FEE_COLLECTOR,
  } = this.config;

  const networkPubkey = NETWORK;

  const networkAccount = await this.connection.getAccountInfo(networkPubkey);
  if (!networkAccount) {
    throw new Error('no network account');
  }
  const decodedNetworkAccount = decodeNetwork(Buffer.from(networkAccount.data));

  const protocolMintPubkey = new PublicKey(decodedNetworkAccount.protocolToken);
  const settlementMintPubkey = new PublicKey(
    decodedNetworkAccount.settlementToken
  );

  const networkProtocolFeeCollectorPubkey =
    await findNetworkProtocolFeeCollectorPubkey(
      networkWalletPubkey,
      protocolMintPubkey
    );

  const networkSettlementFeeCollectorPubkey =
    await findNetworkSettlementFeeCollectorPubkey(
      networkWalletPubkey,
      settlementMintPubkey
    );

  const marketProtocolFeeCollectorPubkey =
    await findMarketProtocolFeeCollectorPubkey(
      marketWalletPubkey,
      protocolMintPubkey
    );

  const marketSettlementFeeCollectorPubkey =
    await findMarketSettlementFeeCollectorPubkey(
      marketWalletPubkey,
      settlementMintPubkey
    );

  const [
    networkProtocolFeeCollectorAccount,
    networkSettlementFeeCollectorAccount,
    marketProtocolFeeCollectorAccount,
    marketSettlementFeeCollectorAccount,
  ] = await this.connection.getMultipleAccountsInfo([
    NETWORK_PROTOCOL_FEE_COLLECTOR ?? networkProtocolFeeCollectorPubkey,
    networkSettlementFeeCollectorPubkey,
    MARKET_PROTOCOL_FEE_COLLECTOR ?? marketProtocolFeeCollectorPubkey,
    marketSettlementFeeCollectorPubkey,
  ]);

  if (networkProtocolFeeCollectorAccount) {
    const decodedProtocolFeeCollector = decodeAccount(
      networkProtocolFeeCollectorAccount.data
    );
    networkProtocolFeeAmount = Number(decodedProtocolFeeCollector.amount) || 0;
  }

  if (networkSettlementFeeCollectorAccount) {
    const decodedSettlementFeeCollector = decodeAccount(
      networkSettlementFeeCollectorAccount.data
    );
    networkSettlementFeeAmount = Number(decodedSettlementFeeCollector.amount) || 0;
  }

  if (marketProtocolFeeCollectorAccount) {
    const decodedProtocolFeeCollector = decodeAccount(
      marketProtocolFeeCollectorAccount.data
    );
    marketProtocolFeeAmount = Number(decodedProtocolFeeCollector.amount) || 0;
  }

  if (marketSettlementFeeCollectorAccount) {
    const decodedSettlementFeeCollector = decodeAccount(
      marketSettlementFeeCollectorAccount.data
    );
    marketSettlementFeeAmount = Number(decodedSettlementFeeCollector.amount) || 0;
  }

  return {
    network: {
      protocolFeeAccountPubkey:
        NETWORK_PROTOCOL_FEE_COLLECTOR ?? networkProtocolFeeCollectorPubkey,
      protocolFeeAmount: networkProtocolFeeAmount,
      settlementFeeAccountPubkey: networkSettlementFeeCollectorPubkey,
      settlementFeeAmount: networkSettlementFeeAmount,
    },
    market: {
      protocolFeeAccountPubkey:
        MARKET_PROTOCOL_FEE_COLLECTOR ?? marketProtocolFeeCollectorPubkey,
      protocolFeeAmount: marketProtocolFeeAmount,
      settlementFeeAccountPubkey: marketSettlementFeeCollectorPubkey,
      settlementFeeAmount: marketSettlementFeeAmount,
    },
  };
}
