/* eslint-disable no-console */
import {
  Keypair,
  Commitment,
  Connection,
  SignatureStatus,
  SimulatedTransactionResponse,
  Transaction,
  TransactionInstruction,
  TransactionSignature,
  Blockhash,
  FeeCalculator,
  RpcResponseAndContext,
} from '@solana/web3.js';
import { WalletSigner } from '../types';

interface BlockhashAndFeeCalculator {
  blockhash: Blockhash;
  feeCalculator: FeeCalculator;
}

export type TransactionState = {
  transactionId: string;
  slot: number;
};

const DEFAULT_TIMEOUT = 15000;

export const getUnixTs = (): number => {
  return new Date().getTime() / 1000;
};

export function sleep(ms: number): Promise<void> {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

const simulateTransaction = async (
  connection: Connection,
  transaction: Transaction,
  commitment: Commitment
): Promise<RpcResponseAndContext<SimulatedTransactionResponse>> => {
  transaction.recentBlockhash = (
    await connection.getRecentBlockhash()
  ).blockhash;

  const signData = transaction.serializeMessage();
  // @ts-ignore
  const wireTransaction = transaction._serialize(signData);
  const encodedTransaction = wireTransaction.toString('base64');
  const config: any = { encoding: 'base64', commitment }; // eslint-disable-line
  const args = [encodedTransaction, config];

  // @ts-ignore
  const res = await connection._rpcRequest('simulateTransaction', args);
  if (res.error) {
    throw new Error('failed to simulate transaction: ' + res.error.message);
  }
  return res.result;
};

const awaitTransactionSignatureConfirmation = async (
  transactionId: TransactionSignature,
  timeout: number,
  connection: Connection
): Promise<SignatureStatus | null | void> => {
  let done = false;
  let status: SignatureStatus | null | void = {
    slot: 0,
    confirmations: 0,
    err: null,
  };
  let subId = 0;

  // eslint-disable-next-line
  status = await new Promise(async (resolve, reject) => {
    setTimeout(() => {
      if (done) {
        return;
      }
      done = true;
      console.log('Timed out for txid', transactionId);
      reject({ timeout: true });
    }, timeout);
    try {
      subId = connection.onSignature(
        transactionId,
        (result, context) => {
          done = true;
          status = {
            err: result.err,
            slot: context.slot,
            confirmations: 0,
          };
          if (result.err) {
            console.log('Rejected via websocket', result.err);
            reject(status);
          } else {
            console.log('Resolved via websocket', result);
            resolve(status);
          }
        },
        'recent'
      );
    } catch (e) {
      done = true;
      console.error('WS error in setup', transactionId, e);
    }

    while (!done) {
      // eslint-disable-next-line
      (async () => {
        try {
          const signatureStatuses = await connection.getSignatureStatuses([
            transactionId,
          ]);
          status = signatureStatuses && signatureStatuses.value[0];
          console.log(
            `https://explorer.solana.com/tx/${transactionId}?cluster=devnet`
          );
          if (!done) {
            if (!status) {
              console.log('REST null result for', transactionId, status);
            } else if (status.err) {
              console.log('REST error for', transactionId, status);
              done = true;
              reject(status.err);
            } else if (!status.confirmations) {
              console.log('REST no confirmations for', transactionId, status);
            } else {
              console.log('REST confirmation for', transactionId, status);
              done = true;
              resolve(status);
            }
          }
        } catch (err) {
          if (!done) {
            console.log(
              'REST connection error: transactionId',
              transactionId,
              err
            );
          }
        }
      })();
      await sleep(2000);
    }
  });

  //@ts-ignore
  if (connection._signatureSubscriptions?.[subId]) {
    connection.removeSignatureListener(subId);
  }
  done = true;
  console.log('Returning status', status);
  return status;
};

export const sendSignedTransaction = async ({
  connection,
  signedTransaction,
  retry = true,
  timeout = DEFAULT_TIMEOUT,
  confirmation = true,
}: {
  signedTransaction: Transaction;
  connection: Connection;
  sendingMessage?: string;
  sentMessage?: string;
  successMessage?: string;
  retry?: boolean;
  timeout?: number;
  confirmation?: boolean;
}): Promise<TransactionState> => {
  const rawTransaction = signedTransaction.serialize();
  const startTime = getUnixTs();
  let slot = 0;
  const transactionId = await connection.sendRawTransaction(rawTransaction, {
    skipPreflight: true,
  });

  let done = false;

  if (retry) {
    (async () => {
      while (!done && getUnixTs() - startTime < timeout) {
        connection
          .sendRawTransaction(rawTransaction, {
            skipPreflight: true,
          })
          .catch((error) => console.error(error));
        await sleep(500);
      }
    })();
  } else {
    connection
      .sendRawTransaction(rawTransaction, {
        skipPreflight: true,
      })
      .catch((error) => console.error(error));
  }

  if (confirmation) {
    try {
      console.log('Started awaiting confirmation for', transactionId);

      const confirmation = await awaitTransactionSignatureConfirmation(
        transactionId,
        timeout,
        connection
      );

      if (!confirmation)
        throw new Error('Timed out awaiting confirmation on transaction');

      if (confirmation.err) {
        console.error(confirmation.err);
        throw new Error('Transaction failed: Custom instruction error');
      }

      slot = confirmation?.slot || 0;
    } catch (err) {
      console.error('Timeout Error caught', err);
      // eslint-disable-next-line
      if ((err as any).timeout) {
        throw new Error('Timed out awaiting confirmation on transaction');
      }

      let simulateResult: SimulatedTransactionResponse | null = null;
      try {
        simulateResult = (
          await simulateTransaction(connection, signedTransaction, 'single')
        ).value;
      } catch (err) {
        console.error('simulate error', err);
      }

      if (simulateResult && simulateResult.err) {
        if (simulateResult.logs) {
          for (let i = simulateResult.logs.length - 1; i >= 0; --i) {
            const line = simulateResult.logs[i];
            if (line.startsWith('Program log: ')) {
              throw new Error(
                'Transaction failed: ' + line.slice('Program log: '.length)
              );
            }
          }
        }
        throw new Error(JSON.stringify(simulateResult.err));
      }
      throw new Error('Transaction failed');
    } finally {
      done = true;
    }

    console.log('Latency', transactionId, getUnixTs() - startTime);
  }

  return { transactionId, slot };
};

export const sendTransaction = async (
  connection: Connection,
  userWallet: WalletSigner,
  instructions: TransactionInstruction[],
  signers: Keypair[],
  commitment: Commitment = 'singleGossip',
  includesFeePayer: boolean = false,
  retry: boolean = true,
  confirmation: boolean = true,
  block?: BlockhashAndFeeCalculator,
  beforeSend?: () => void
): Promise<TransactionState | undefined> => {
  if (!userWallet.publicKey) throw new Error('wallet is not connected');

  let transaction = new Transaction();
  instructions.forEach((instruction) => transaction.add(instruction));
  transaction.recentBlockhash = (
    block || (await connection.getRecentBlockhash(commitment))
  ).blockhash;

  if (includesFeePayer) {
    transaction.setSigners(...signers.map((s) => s.publicKey));
  } else {
    transaction.setSigners(
      // fee payed by the wallet owner
      userWallet.publicKey,
      ...signers.map((s) => s.publicKey)
    );
  }

  if (signers.length > 0) {
    transaction.partialSign(...signers);
  }

  if (!includesFeePayer) {
    transaction = await userWallet.signTransaction(transaction);
  }

  if (beforeSend) {
    beforeSend();
  }

  try {
    const { transactionId, slot } = await sendSignedTransaction({
      connection,
      signedTransaction: transaction,
      retry,
      confirmation,
    });

    return { transactionId, slot };
  } catch (error) {
    console.error(error);
    return undefined;
  }
};
