import BN from 'bn.js';

import { FixedFees } from './FixedFees';

export class Market {
  version: number;
  programId: string;
  bumpSeed: number;
  authority: string;
  network: string;
  underlyingProductOracle: string;
  underlyingPriceOracle: string;
  duration: BN;
  contractSize: BN;
  protocolFeeCollector: string;
  settlementFeeCollector: string;
  fee: FixedFees;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    authority: string;
    network: string;
    underlyingProductOracle: string;
    underlyingPriceOracle: string;
    duration: BN;
    contractSize: BN;
    protocolFeeCollector: string;
    settlementFeeCollector: string;
    fee: FixedFees;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.authority = args.authority;
    this.network = args.network;
    this.underlyingProductOracle = args.underlyingProductOracle;
    this.underlyingPriceOracle = args.underlyingPriceOracle;
    this.duration = args.duration;
    this.contractSize = args.contractSize;
    this.protocolFeeCollector = args.protocolFeeCollector;
    this.settlementFeeCollector = args.settlementFeeCollector;
    this.fee = args.fee;
  }
}
