import BN from 'bn.js';

export class LinearFees {
  enum: number = 2;
  version: number;
  protocolTokenRequiredForMinFee: BN;
  minProtocolFeeBps: number;
  maxProtocolFeeBps: number;
  minSettlementFeeBps: number;
  maxSettlementFeeBps: number;
  _padding1: number = 0;
  _padding2: number = 0;
  _padding4: number = 0;
  _padding8: BN = new BN(0);
  _padding8_0: BN = new BN(0);
  _padding8_1: BN = new BN(0);
  _padding8_2: BN = new BN(0);
  _padding8_3: BN = new BN(0);

  constructor(args: {
    version: number;
    protocolTokenRequiredForMinFee: BN;
    minProtocolFeeBps: number;
    maxProtocolFeeBps: number;
    minSettlementFeeBps: number;
    maxSettlementFeeBps: number;
  }) {
    this.version = args.version;
    this.protocolTokenRequiredForMinFee = args.protocolTokenRequiredForMinFee;
    this.minProtocolFeeBps = args.minProtocolFeeBps;
    this.maxProtocolFeeBps = args.maxProtocolFeeBps;
    this.minSettlementFeeBps = args.minSettlementFeeBps;
    this.maxSettlementFeeBps = args.maxSettlementFeeBps;
  }
}
