import { AccountInfo, PublicKey } from '@solana/web3.js';
import _get from 'lodash/get';

import { MIN_1, MIN_5, MIN_15, HR_1, DAY_1 } from '../constants';
import { Version } from '../borsh';
import {
  MarketPairEnum,
  MarketPubkey,
  ParimutuelAccount,
  MarketStatusEnum,
} from '../types';
import { ParimutuelConfig } from '../web3';

export const getProtocolMint = (
  config: ParimutuelConfig,
  marketPair: MarketPairEnum
): PublicKey => _get(config.markets, [marketPair, 'PROTOCOL_MINT']);

export const getSettlementMint = (
  config: ParimutuelConfig,
  marketPair: MarketPairEnum
): PublicKey => _get(config.markets, [marketPair, 'SETTLEMENT_MINT']);

export const getMarketPubkeys = (
  config: ParimutuelConfig,
  marketPair: MarketPairEnum
): MarketPubkey[] => {
  const market = [];

  if (_get(config.markets, [marketPair, 'MARKET_60S'])) {
    market.push({
      pubkey: _get(config.markets, [marketPair, 'MARKET_60S']),
      duration: MIN_1,
    });
  }

  if (_get(config.markets, [marketPair, 'MARKET_300S'])) {
    market.push({
      pubkey: _get(config.markets, [marketPair, 'MARKET_300S']),
      duration: MIN_5,
    });
  }

  if (_get(config.markets, [marketPair, 'MARKET_900S'])) {
    market.push({
      pubkey: _get(config.markets, [marketPair, 'MARKET_900S']),
      duration: MIN_15,
    });
  }

  if (_get(config.markets, [marketPair, 'MARKET_3600S'])) {
    market.push({
      pubkey: _get(config.markets, [marketPair, 'MARKET_3600S']),
      duration: HR_1,
    });
  }

  if (_get(config.markets, [marketPair, 'MARKET_86400S'])) {
    market.push({
      pubkey: _get(config.markets, [marketPair, 'MARKET_86400S']),
      duration: DAY_1,
    });
  }

  return market;
};

export const isExpired = (parimutuel: ParimutuelAccount): boolean => {
  const { expired } = parimutuel.info.parimutuel;
  return Boolean(expired);
};

export const isLong = (parimutuel: ParimutuelAccount): boolean => {
  const { strike, index } = parimutuel.info.parimutuel;
  return index.toNumber() > strike.toNumber();
};

export const getMarketStatus = (
  timeWindowStart: string,
  marketClose: string,
  duration: number
): MarketStatusEnum => {
  const currentTime = new Date().getTime();

  if (currentTime >= Number(marketClose) + duration * 1000)
    return MarketStatusEnum.SETTLED;

  if (
    currentTime >= Number(timeWindowStart) &&
    currentTime < Number(marketClose) + duration * 1000 // TODO: remove duration
  ) {
    return MarketStatusEnum.LIVE;
  }

  return MarketStatusEnum.UPCOMING;
};

export const isMarketAccount = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.MarketV1;

export const isNetworkAccount = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.NetworkV1;

export const isStoreAccount = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.StoreV1;

export const isParimutuelAccount = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.ParimutuelV1;

export const isTraderAccount = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.TraderAccountV1;

export const isTraderSequenceNumber = (account: AccountInfo<Buffer>): boolean =>
  account.data[0] === Version.TraderSequenceNumberV1;

export const isTraderPositionAccount = (
  account: AccountInfo<Buffer>
): boolean => account.data[0] === Version.ParimutuelTraderPositionAccountV1;
