import { PublicKey } from '@solana/web3.js';

import { ParimutuelWeb3 } from '../web3';
import {
  findNetworkTraderAccountPubkey,
  findNetworkTraderFeePayerPubkey,
  decodeAccount,
} from '../utils';
import { ParimutuelTraderFeePayerAccount } from '../types';

export async function getTraderFeePayerAccount(
  this: ParimutuelWeb3,
  userWalletPubkey: PublicKey,
  networkPubkey: PublicKey
): Promise<ParimutuelTraderFeePayerAccount> {
  const { PARIMUTUEL_PROGRAM_ID } = this.config;

  const networkTraderAccountPubkey = await findNetworkTraderAccountPubkey(
    PARIMUTUEL_PROGRAM_ID,
    userWalletPubkey,
    networkPubkey
  );

  const networkTraderFeePayerPubkey = await findNetworkTraderFeePayerPubkey(
    PARIMUTUEL_PROGRAM_ID,
    networkTraderAccountPubkey
  );

  const traderFeePayerAccount = await this.connection.getAccountInfo(
    networkTraderFeePayerPubkey
  );

  if (!traderFeePayerAccount) {
    throw new Error('no fee payer account');
  }

  return {
    pubkey: networkTraderFeePayerPubkey,
    account: traderFeePayerAccount,
    info: {
      tokenAccount: {
        pubkey: networkTraderFeePayerPubkey.toBase58(),
        account: traderFeePayerAccount,
        info: decodeAccount(traderFeePayerAccount.data),
      },
    },
  };
}
