import BN from 'bn.js';
import { FixedFees } from './FixedFees';
import { LinearFees } from './LinearFees';
import { TwoTieredFees } from './TwoTieredFees';

export type DynamicFees = LinearFees | TwoTieredFees;

export class NetworkArgs {
  instruction: number = 0;
  fees: DynamicFees;

  constructor(args: { fees: DynamicFees }) {
    this.fees = args.fees;
  }
}

export class NetworkUpdateArgs {
  instruction: number = 14;
  fees: DynamicFees;

  constructor(args: { fees: DynamicFees }) {
    this.fees = args.fees;
  }
}

export class MarketArgs {
  instruction: number = 1;
  duration: BN;
  contractSize: BN;
  fees: FixedFees;

  constructor(args: {
    duration: number;
    contractSize: number;
    fees: FixedFees;
  }) {
    this.duration = new BN(args.duration);
    this.contractSize = new BN(args.contractSize);
    this.fees = args.fees;
  }
}

export class StoreArgs {
  instruction: number = 2;
  fees: FixedFees;

  constructor(args: { fees: FixedFees }) {
    this.fees = args.fees;
  }
}

export class ParimutuelArgs {
  instruction: number = 3;
  marketOpen: BN;
  marketClose: BN;
  timeWindowStart: BN;

  constructor(args: {
    marketOpen: number;
    marketClose: number;
    timeWindowStart: number;
  }) {
    this.marketOpen = new BN(args.marketOpen);
    this.marketClose = new BN(args.marketClose);
    this.timeWindowStart = new BN(args.timeWindowStart);
  }
}

export class UpdatePriceArgs {
  instruction: number = 4;
}

export class TraderAccountsArgs {
  instruction: number = 5;
}

export class TraderPositionAccountArgs {
  instruction: number = 6;
}

export class TraderSequenceNumberArgs {
  instruction: number = 7;
}

export class AttachCookieArgs {
  instruction: number = 8;
}

export class UpdatePositionArgs {
  instruction: number = 9;
  size: BN;
  side: number;
  seq: BN;

  constructor(args: { size: number; side: number, seq: number }) {
    this.size = new BN(args.size);
    this.side = args.side;
    this.seq = new BN(args.seq);
  }
}

export class SettlePositionArgs {
  instruction: number = 10;
}

export class NetworkTraderFeePayerArgs {
  instruction: number = 11;
}

export class WithdrawFeeArgs {
  instruction: number = 12;
  amount: BN;

  constructor(args: { amount: number }) {
    this.amount = new BN(args.amount);
  }
}

export class DestroyParimutuelArgs {
  instruction: number = 15;
}

export class DestroyPositionArgs {
  instruction: number = 18;
}

export class DestroyPositionEntryArgs {
  instruction: number = 17;
}
