import BN from 'bn.js';

export class TraderAccount {
  version: number;
  programId: string;
  bumpSeed: number;
  traderWallet: string;
  aggregationLevelKey: string;
  profit: BN;
  loss: BN;
  wins: BN;
  losses: BN;

  constructor(args: {
    version: number;
    programId: string;
    bumpSeed: number;
    traderWallet: string;
    aggregationLevelKey: string;
    profit: number;
    loss: number;
    wins: number;
    losses: number;
  }) {
    this.version = args.version;
    this.programId = args.programId;
    this.bumpSeed = args.bumpSeed;
    this.traderWallet = args.traderWallet;
    this.aggregationLevelKey = args.aggregationLevelKey;
    this.profit = new BN(args.profit);
    this.loss = new BN(args.loss);
    this.wins = new BN(args.wins);
    this.losses = new BN(args.losses);
  }
}
