import {
  PublicKey,
  GetProgramAccountsConfig,
  MemcmpFilter,
  DataSizeFilter,
} from '@solana/web3.js';
import _get from 'lodash/get';

import { ParimutuelWeb3 } from '../web3';
import {
  decodeParimutuel,
  decodeTraderPositionAccount,
  getMultipleAccounts,
} from '../utils';
import { ParimutuelMarket, MarketInfo, ParimutuelPosition } from '../types';

export async function getUserPositions(
  this: ParimutuelWeb3,
  userWalletPubkey: PublicKey,
  markets: ParimutuelMarket[]
): Promise<ParimutuelPosition[]> {
  const { PARIMUTUEL_PROGRAM_ID } = this.config;

  const traderPositionAccounts = await this.connection.getProgramAccounts(
    PARIMUTUEL_PROGRAM_ID,
    {
      filters: [
        { dataSize: 172 } as DataSizeFilter,
        {
          memcmp: {
            offset: 74,
            bytes: userWalletPubkey?.toBase58(),
          },
        } as MemcmpFilter,
      ],
    } as GetProgramAccountsConfig
  );

  const decodedAccounts = traderPositionAccounts.map((account) =>
    decodeTraderPositionAccount(account.account.data)
  );

  const parimutuelAccounts = await getMultipleAccounts(
    this.connection,
    decodedAccounts.map((account) => new PublicKey(account.parimutuel))
  );

  const positions = traderPositionAccounts.map((account, index) => {
    const parimutuel = decodeParimutuel(
      _get(parimutuelAccounts, [index, 'account', 'data']) as Buffer
    );
    const position = decodeTraderPositionAccount(account.account.data);

    return {
      pubkey: account.pubkey,
      account: account.account,
      info: {
        parimutuelPubkey: new PublicKey(position.parimutuel),
        parimutuel,
        market: _get(markets, [parimutuel.marketKey, 'info']) as MarketInfo,
        position,
      },
    };
  });

  return positions;
}

export async function getParimutuelPositions(
  this: ParimutuelWeb3,
  parimutuelPubkey: PublicKey
): Promise<ParimutuelPosition[]> {
  const { PARIMUTUEL_PROGRAM_ID } = this.config;

  const traderPositionAccounts = await this.connection.getProgramAccounts(
    PARIMUTUEL_PROGRAM_ID,
    {
      filters: [
        { dataSize: 172 } as DataSizeFilter,
        {
          memcmp: {
            offset: 42,
            bytes: parimutuelPubkey?.toBase58(),
          },
        } as MemcmpFilter,
      ],
    } as GetProgramAccountsConfig
  );

  const decodedAccounts = traderPositionAccounts.map((account) =>
    decodeTraderPositionAccount(account.account.data)
  );

  const parimutuelAccounts = await getMultipleAccounts(
    this.connection,
    decodedAccounts.map((account) => new PublicKey(account.parimutuel))
  );

  const positions = traderPositionAccounts.map((account, index) => {
    const parimutuel = decodeParimutuel(
      _get(parimutuelAccounts, [index, 'account', 'data']) as Buffer
    );
    const position = decodeTraderPositionAccount(account.account.data);

    return {
      pubkey: account.pubkey,
      account: account.account,
      info: {
        parimutuelPubkey: new PublicKey(position.parimutuel),
        parimutuel,
        market: _get([], [parimutuel.marketKey, 'info']) as MarketInfo,
        position,
      },
    };
  });

  return positions;
}
