import {
  SystemProgram,
  SYSVAR_RENT_PUBKEY,
  TransactionInstruction,
  PublicKey,
} from '@solana/web3.js';
import { serialize } from 'borsh';

import { NetworkArgs, schema } from '../borsh';

export const createNetworkInstruction = async (
  programId: PublicKey,
  settings: NetworkArgs,
  networkPubkey: PublicKey,
  networkWalletPubkey: PublicKey,
  payerPubkey: PublicKey,
  protocolProductOraclePubkey: PublicKey,
  protocolPriceOraclePubkey: PublicKey,
  networkProtocolFeeCollectorPubkey: PublicKey,
  networkSettlementFeeCollectorPubkey: PublicKey,
  protocolMintPubkey: PublicKey,
  settlementMintPubkey: PublicKey
): Promise<TransactionInstruction> => {
  const data = Buffer.from(serialize(schema, settings));

  return new TransactionInstruction({
    keys: [
      {
        pubkey: networkPubkey,
        isSigner: false,
        isWritable: true,
      },
      {
        pubkey: protocolMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolProductOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: protocolPriceOraclePubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: settlementMintPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkWalletPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: networkProtocolFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: networkSettlementFeeCollectorPubkey,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: payerPubkey,
        isSigner: true,
        isWritable: false,
      },
      {
        pubkey: SYSVAR_RENT_PUBKEY,
        isSigner: false,
        isWritable: false,
      },
      {
        pubkey: SystemProgram.programId,
        isSigner: false,
        isWritable: false,
      },
    ],
    programId: programId,
    data: data,
  });
};


